import React, { Component } from 'react';
import { observer } from "mobx-react";
import {
  MaterialBox, MaterialDivider, MaterialTextField
} from 'web-app/apps/platform/components/material'
import { Redirect } from 'react-router-dom'
import PlatformObjectCard from 'web-app/apps/platform/components/PlatformObjectCard.react'
import PlatformBasicButton from 'web-app/apps/platform/components/PlatformBasicButton.react'
import UserStore from 'web-app/apps/platform/stores/User.store'
import theme from 'web-app/themes/Basic.theme'
import PlatformSnackbar from 'web-app/apps/platform/components/PlatformSnackbar.react'

class UsersOpCard extends Component {
  async _userCreate () {
    await UserStore.userCreate().then( res => {
      if ( res === "success" )
      {
        window.location.assign( '/platform/users/list' )
      }
  })
  }


  async _userUpdate () {
    await UserStore.userUpdate().then( res => {
      if ( res === "success" ) {
        window.location.assign('/platform/users/list')
      } else {
        alert("unable to update the user")
      }
    })
  }


  _renderObjectOpCard () {
    // const status=UserStore.getStatus()
    const selectedUsers = UserStore.getSelectedUsers()
    const userName = UserStore.getUserName()
    const name=UserStore.getName()
    return (
      <MaterialBox style={{ width: '100%', marginTop: 30 }}>
        <MaterialBox style={{ width: '100%' }}>
          <MaterialTextField
            fullWidth label='Name' variant='outlined' size='small'
            onChange={( event ) => UserStore.setName( event.target.value )}
            value={name ? name : ''}
          />
        </MaterialBox>
        <MaterialBox style={ { width: '100%', marginTop:30 } }>
          <MaterialTextField
            fullWidth label='Email' variant='outlined' size='small'
            InputProps={{placeholder:"Enter email ex. caXXXX@delta.org"}}

            onChange={ ( event ) => UserStore.setUserName( event.target.value ) }
            value={ userName ? userName : '' }
          />
        </MaterialBox>

        <MaterialBox style={ { width: '100%', marginTop: 30 } }>
          <PlatformBasicButton
            radius={ 10 }
            bgColor={ theme.primaryDark }
            borderColor={ theme.primaryDark }
            fontColor={ theme.textPrimary }
            onClick={ async () => {
              selectedUsers?this._userUpdate():this._userCreate()
            } }
          >
            { selectedUsers? "UPDATE":"ADD"}
          </PlatformBasicButton>
        </MaterialBox>
        {/* {status ? <PlatformSnackbar key={status.key} message={status.message}></PlatformSnackbar>:""} */}
      </MaterialBox>
    )
  }


  render () {
    const selectedUsers=UserStore.getSelectedUsers()
    if ( this.props.location.pathname == '/platform/users/update' ) {
      if ( !selectedUsers ) {
        return (<Redirect to= '/platform/users/list'></Redirect>);
      }
    }
    return (
      <PlatformObjectCard width={ 600 }>
        { this._renderObjectOpCard() }
      </PlatformObjectCard>
    )
  }
}

UsersOpCard.displayName = 'UsersOpCard';
export default observer( UsersOpCard );
